.body {
    display: grid;
    grid-template-rows: 2fr 1fr;
    margin: 20px auto;
    padding: 10px;
    width: 400px;
    height: 400px;
    border: #2399cc 3px solid;
    border-radius: 3%;
    background-color: #233740;
}

.result {
    margin: 10px;
    color: #33c2ff;
    border: #33c2ff 3px solid;
    border-radius: 3%;
    text-align: center;
    font-size: 60px;
    font-weight: bolder;
    padding: 80px;
}
.titleItem {
    margin: 10px;
    color: #33c2ff;
    border: #33c2ff 3px solid;
    border-radius: 3%;
    text-align: center;
    font-size: 28px;
    font-weight: bolder;
    padding: 80px;
}
.titleItemError{
    margin: 10px;
    color: red;
    border: #33c2ff 3px solid;
    border-radius: 3%;
    text-align: center;
    font-size: 28px;
    font-weight: bolder;
    padding: 80px;
}
.inputBoard {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    background-color: #233740;
    margin: 10px;
    border: #33c2ff 3px solid;
    border-radius: 4%;

}
.inputItem{
    margin: 20px auto;
    padding: 10px;
    color: #33c2ff;
    font-weight: bold;
    font-size: 24px;
}

.clickBoard {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0 10px 10px 10px;
    border: #33c2ff 3px solid;
    border-radius: 3%;
}
.clickBoarCounterSet{
    display: grid;
    grid-template-columns: 200px ;
    margin: 5px auto;
    padding: 0 85px 0 85px;
    border: #33c2ff 3px solid;
    border-radius: 3%;
}

.buttonComponent {
    margin: 30px;
    background-color: #33c2ff;
    border-radius: 10%;
    font-size: xxx-large;
    font-weight: bolder;
}
input{
    border-radius: 10%;
    border: #33c2ff 3px solid;
    width: 100px;
    height: 20px;
    text-align: center;
}

.error {
    margin: 10px;
    border: #33c2ff 3px solid;
    border-radius: 3%;
    text-align: center;
    font-size: 90px;
    font-weight: bolder;
    padding: 50px;
    color: red;
}
.buttonStyle{
    margin: 0 auto;
}
.errorInput{
    background: red;
}